import React from 'react';
import { motion, useAnimation } from 'framer-motion';
import { useInView } from 'react-intersection-observer';
import './InvitationText.css';  // CSS 파일 임포트

const InvitationText = () => {
  const controls = useAnimation();
  const [ref, inView] = useInView({
    triggerOnce: false,
    threshold: 0.2,
  });

  React.useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      const windowHeight = window.innerHeight;
      const threshold = windowHeight * 0.1;

      if (currentScrollY < threshold) {
        controls.start({ opacity: 0, y: 100 });
      } else {
        controls.start({ opacity: 1, y: 0 });
      }
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [controls]);

  return (
    <motion.div
      ref={ref}
      className="invitation-text"  // 스타일 클래스 추가
      initial={{ opacity: 0, y: 100 }}
      animate={controls}
      transition={{ duration: 0.8 }}
    >
      <h2>INVITATION</h2>
      <p>서로 다른 길을 걸어온 저희가 함께하는</p>
      <p>다섯 번째 겨울을 끝으로</p>
      <p>부부의 연을 맺어 항상 함께하기로 했습니다</p>
      <p className="extra-space">인생의 또 다른 시작을 하기로 한 날</p>
      <p>귀한 걸음 하시어 축복해 주시면 감사하겠습니다</p>
      <div className="names">
        <p>성천모 . 이미자의 아들 성기덕</p>
        <p>노장열 . 주정하의 딸 노승혜</p>
      </div>
    </motion.div>
  );
};

export default InvitationText;
